import News from "@/components/News.vue";
export default {
	components: {
		News
	},
	
	props: ["infoArr"],
	
	data() {
		return {

		}
	},

	mounted: function(){
		
	},
	
	methods: {
		
		// 列表详情数据存储
		goNext(index,phone){
			var self = this
			var obj = {}
			obj = this.infoArr[index]
			
			//填写的数据缓存
			uni.setStorage({
				key: 'obj',
				data: obj,
				success: function() {
					uni.navigateTo({
						url: '/pages/home/contact/external?phone='+phone
					})
				}
			});
			
		},

	}
}
